import { Ticket } from "generated/prisma";
import { ExpandedTicket } from "../types/prisma.types";

export function isExpandedTicket(ticket: Ticket | ExpandedTicket): ticket is ExpandedTicket {
  return (
    "client" in ticket ||
    "department" in ticket ||
    "employees" in ticket ||
    "ticketItems" in ticket 
  );
}
